<template>
    <div class="picture">

        <div class="line">
            <el-table :data="tableData" stripe style="width: 100%" ref="multipleTable">
                <el-table-column prop="teacherId" label="ID" width="100">
                </el-table-column>
                <el-table-column prop="name" label="">
                </el-table-column>
                <el-table-column prop="mobile" label="">
                </el-table-column>


                <el-table-column label="" width="">
                    <template scope="scope">
                        <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                        <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
                    </template>
                </el-table-column>

            </el-table>
        </div>

        <div class="line pr">
            <el-button type="primary" @click="showDialog"></el-button>
            <el-pagination layout="prev, pager, next" :total="count" @current-change="handleCurrentChange">
            </el-pagination>
        </div>


         <el-dialog title="" :visible.sync="appointDialog" :before-close="handleClose(appointDialog)" class="dialogSzie">

                <el-select v-model="klassValue" placeholder="">
                      <el-option
                        v-for="item in klassOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                  </el-select> 

                  <el-select v-model="courseValue" placeholder="">
                      <el-option
                        v-for="item in courseOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                  </el-select> 


            <span slot="footer" class="dialog-footer">
                <el-button @click="isAdd(1)" > </el-button>
                <el-button @click="isAdd(2)" type="primary"> </el-button>
            </span>

        </el-dialog>



        <el-dialog title="" :visible.sync="dialogVisible" :before-close="handleClose(dialogVisible)" class="dialogSzie">

            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="form.mobile"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.username"></el-input>
                    </el-input>
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="isAdd(1)" > </el-button>
                <el-button @click="isAdd(0)" type="primary"> </el-button>
            </span>

        </el-dialog>


        <el-dialog title="" :visible.sync="GradeDialog" :before-close="handleClose(GradeDialog)" class="dialogSzie">

            <el-form ref="form" :model="form" label-width="80px">

                <el-form-item label="">
                     {{this.teacherName}}
                  </el-form-item>

                <el-form-item label="">
                    <div v-for="(item,index) in gradeList">
                        <div class="grade">{{item.name}} {{item.description}} <span class="del" @click="resetGrade(index)">X</span></div>
                    </div>
                </el-form-item>

                <el-form-item label="">
                    <div class="line">
                        <el-input v-model="newGrade" placeholder=""></el-input>
                    </div>
                    <div class="line">
                        <el-input v-model="newDescription" placeholder=""></el-input>
                    </div>
                    <div class="line">
                        <el-button type="primary" @click="addGrade"></el-button>
                    </div>
                </el-form-item>
            </el-form>



            <span slot="footer" class="dialog-footer">
                <el-button @click="GisAdd(1)" > </el-button>
                <el-button @click="GisAdd(0)" type="primary"> </el-button>
            </span>

        </el-dialog>

    </div>

</template>

<script>
    export default {
        name: 'picture',
        data() {
            return {
                tableData: [],
                teacherId:"",
                teacherName: "",

                dialogVisible: false, // 
                dialogUsage: 0, // 0  1
                dialogIndex: '',

                klassValue:'',
                courseValue: '',

                appointDialog: false, //
                klassOptions: [],
                courseOptions: [],

                GradeDialog: false, //  
                GradeUsage: 0, // 0  1
                GradeIndex: '',

                isResetG: false, // dialog


                showSize: 10,
                current_page: 0,
                count: 100,

                form: {
                    name: '',
                    description: '',
                },

                gradeList: [],

                newGrade: '',
                newDescription: '',

                mobile:'',
                username:''
            }
        },
        watch: {
            current_page(now, old) {
                this.getData();
                this.initCourse();
            },
        },
        methods: {
            message() {
                this.$message('');
            },
            getData() {
                this.$http({
                    method: 'get',
                    url: 'http://k12soft.vwico.com:8080/management/teachers?schoolId='+JSON.parse(localStorage.actor).school.id,
                    headers: {
                        // 'content-type': "multipart/form-data",
                        'Authorization': 'Bearer ' + localStorage.token

                    }
                }).then((res) => { // promise
                    const {
                        body
                    } = res

                    console.log("====get teachersbody ======",res.body);

                    var teacherArray=[];
                    res.body.forEach(function(item, index, array){
                        teacherArray.push({teacherId: item.actor.user.id, name:item.actor.user.username, mobile: item.actor.user.mobile})
                    })

                    console.log("------teacherArray------",teacherArray)

                    if (!!body.length) {
                        this.tableData = teacherArray
                        this.count = teacherArray.length
                    } else {
                        alert('err:' + body)
                    }

                })
            },
            initCourse(){
                console.log("------initCourseinitCourse------")
                 this.$http({
                                method: 'get',
                                url: 'http://k12soft.vwico.com:8080/management/courses',

                                headers: {
                                    'Authorization': 'Bearer ' + localStorage.token

                                }
                            }).then((res) => { // promise
                                const {
                                    body
                                } = res

                                console.log("===res getCoursesData===",res.body);

                                if (!!body.length) {
                                    
                                      var courseArray=[];
                                      res.body.forEach(function(item, index, array){
                                           courseArray.push({value:item.id,label:item.name})    
                                      })
                                         //this.courseValue = courseArray[0].value;
                                         this.courseOptions = courseArray;
                                            
                                        } else {
                                            alert('err:' + body)
                                        }
                                    })
            },
            handleClose(dialogName) {
                this[dialogName] = false
            },
            showDialog(path) {
                // this.path = path

                this.form.name = ''
                this.form.description = ''

                this.dialogVisible = true;
                this.appointDialog = false;
                this.dialogUsage = 0

            },
            handleCurrentChange(val) {
                this.current_page = val
            },
            handleEdit(index, row) {


                this.dialogVisible = false;
                this.appointDialog = true;
                this.dialogIndex = index;
                this.teacherId = row.teacherId;
                this.teacherName = row.name;
                this.form = Object.assign({}, row);

                            this.$http({
                                method: 'get',
                                url: 'http://k12soft.vwico.com:8080/management/klasses',

                                headers: {
                                    'Authorization': 'Bearer ' + localStorage.token

                                }
                            }).then((res) => { // promise
                                const {
                                    body
                                } = res

                                console.log("===res getKlassData===",res.body);

                                    if (!!body.length) {
                                    
                                      var klassArray=[];
                                      res.body.forEach(function(item, index, array){
                                           klassArray.push({value:item.id,label:item.name})    
                                      })
                                         //this.klassValue = klassArray[0].value;
                                         this.klassOptions = klassArray;

                                          this.$http({
                                                method: 'get',
                                                url: 'http://k12soft.vwico.com:8080/management/courses',

                                                headers: {
                                                    'Authorization': 'Bearer ' + localStorage.token

                                                }
                                            }).then((res) => { // promise
                                                const {
                                                    body
                                                } = res

                                                console.log("===res getCoursesData===",res.body);

                                                if (!!body.length) {
                                                    
                                                      var courseArray=[];
                                                      res.body.forEach(function(item, index, array){
                                                           courseArray.push({value:item.id,label:item.name})    
                                                      })
                                                         //this.courseValue = courseArray[0].value;
                                                         this.courseOptions = courseArray;
                                                            
                                                        } else {
                                                            alert('err:' + body)
                                                        }
                                                    })
                                            
                                        } else {
                                            alert('err:' + body)
                                        }
                                    })

            },
            handleDelete(index, row) {
                this.tableData.splice(index, 1)
            },
            isAdd(tag) { // 0     1   2 
                this.dialogVisible = false;
                this.appointDialog = false;

                if (tag === 0 && this.dialogUsage == 0) {
                   this.$http.post(
                    //'http://k12soft.vwico.com:8080/management/kindergartens/',
                    'http://k12soft.vwico.com:8080/management/invitations/teacher',
                    {
                        "mobile": this.form.mobile,
                        "username": this.form.username,
                        "joinTime":'0'
                    },
                    {
                      headers:{
                        // 'content-type': "multipart/form-data",
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer '+ localStorage.token

                      }
                    }
                ).then((res) =>{
                    // const {body} = res;
                    console.log("===body=add==teacher====",res);
                    this.getData();
                })

                }

                if (tag === 2 ) {

                    console.log("===this.klassValue====",this.klassValue);
                    console.log("===this.courseValue====",this.courseValue);

                    this.$http.post(
                     'http://k12soft.vwico.com:8080/management/teachings',
                     {
                        "klassId": this.klassValue,
                        "courseId": this.courseValue,
                        "teacherId": this.teacherId
                    },
                    {
                    headers: {
                        // 'content-type': "multipart/form-data",
                        'Authorization': 'Bearer ' + localStorage.token

                    }
                }
                      
                ).then((res) =>{
                    // const {body} = res;
                    console.log("===body=teacheing==body==",res.body);
                    if(res.body){
                        alert("!");
                         this.getData();
                    }
                })
                }

                if (tag === 0 && this.dialogUsage == 1) {
                    this.$http.put(
                     'http://k12soft.vwico.com:8080/management/kindergartens/' + this.teacherId,
                     {
                        "description": this.form.description,
                        "name": this.form.name
                    },
                    {
                    headers: {
                        // 'content-type': "multipart/form-data",
                        'Authorization': 'Bearer ' + localStorage.token

                    }
                }
                      
                ).then((res) =>{
                    // const {body} = res;
                    console.log("===body=1111111====",res);
                     this.getData();
                })
                }


                this.form = {}

            },
            GisAdd(tag) { // 0     1 
                this.GradeDialog = false
            },
            addItem() {
                this.$http({
                    method: 'post',
                    url: this.$getApi + '/image/add_extend',
                    params: {
                        // urls: this.checkList
                    },
                }).then((res) => { // promise
                    const {
                        body
                    } = res

                    if (body.meta.code === 0) {
                        this.$message({
                            message: '',
                            type: 'success'
                        });

                    } else {
                        alert('err:' + body.meta.message)
                        this.$message({
                            message: '' + body.meta.message,
                            type: 'error'
                        });
                    }

                })
            },
            editGrade(index, row) {
                this.GradeDialog = true
                this.teacherName = row.name
                this.newKgId = row.id
                this.gradeId = 1


                this.$http({
                    method: 'get',
                    url:'http://k12soft.vwico.com:8080/management/grades?kgId='+this.gradeId,                  
                    headers: {
                        // 'content-type': "multipart/form-data",
                        'Authorization': 'Bearer ' + localStorage.token

                    }
                }).then((res) => { // promise
                   
                    const {
                        body
                    } = res

                    console.log("===get grades====",body);

                    if(!res.ok){
                        alert('err res:' + res)
                    }

                    if (!!body.length) {
                        this.gradeList = body

                    } else {
                        this.gradeList = []
                    }

                })


            },
            resetGrade(index) {
                this.isResetG = true

                this.gradeList.splice(index, 1)

            },
            addGrade() {

                let _this = this
                this.$http.post(
                    'http://k12soft.vwico.com:8080/management/grades/',
                    //this.$getApi+'management/grades',
                    {
                        "description": this.newDescription,
                        "name": this.newGrade,
                        "kindergartenId": _this.newKgId
                    },
                    {
                      headers:{
                        // 'content-type': "multipart/form-data",
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + localStorage.token

                      }
                    }
                ).then((res) =>{
                    // const {body} = res;
                    console.log("===add grades=res====",res);
                })

                // this.gradeList.push({
                //     grade: this.newGrade,
                //     description: this.newDescription

                // })
                this.newGrade = ''
                this.newDescription = ''
            }
        },
        beforeMount() {

        },
        mounted() {
            this.getData()
        }
    }
</script>
<style scoped>
    /*#mainContent {*/
    /*width: 1000px;*/
    /*margin: 10px;*/
    /*}*/

    .picture {
        margin-left: 10px;
        width: 98%;
        height: 100%;
    }

    .line {
        padding: 10px;
    }

    .pr {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .line .el-pagination {
        padding: 0;
        display: inline-block;
    }

    .imgStyle {
        display: block;
        width: 500px;
        height: auto;
        margin: 10px auto;
    }

    .preImg {
        display: block;
        width: 100px;
        height: auto;
        margin: 0 auto;
    }

    .del {
        padding: 5px;
        color: red;
        cursor: pointer;
        transition: all .3s;
    }

    .del:hover {
        background: #333;
    }

    .grade {
        padding: 4px;
        /*border: 1px solid #33cccc;*/
    }
</style>


